.projects {
    background-color: #c178c2;

    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4% 0;
}
.container {
    height: 80%;
    width: 80%;
    display: flex;
    border: 1px solid;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;


}
.titleContainer{
    height: 100px;
    width: 100%;
    border: 1px solid;
    display: flex;
    justify-content: center;

}
.myProjects{
    height: 70%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;


}
