

.contacts{
    background-color: #ff9b00;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    height: 80%;
    width: 80%;
    display: flex;
    border: 1px solid;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;


}
.titleContainer{
    padding: 0 0 30px 0;
}
.form{
    border: 1px solid;
    width: 300px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.buttonForm{
    padding: 30px 0 0 0;
}

@media (max-width: 500px) {

    .form{
        border: 1px solid;
        width: 80%;
        height: 300px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
}