

.footer{
    background-color: #ffebe6;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    height: 80%;
    width: 80%;
    display: flex;
    border: 1px solid;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;


}
.linkBar{
    width: 40%;
    display: flex;
    justify-content: space-between;
}
@media (max-width: 700px) {
    .linkBar{
        width: 70%;
        display: flex;
        justify-content: space-between;
    }
}